<template>
  <div class="sider-news">
    <h3 style="text-align: center">个性广告推荐</h3>
    <div v-for="(ad, index) in ads" :key="index" class="ad-item">
      <img :src="ad.image" :alt="广告图片" class="ad-image" />
      <p
        class="ad-title"
        :class="{ focused: focusedIndex === index }"
        @mouseover="setFocus(index)"
        @mouseleave="removeFocus"
        @click="HandleCilckAd(ad)"
      >
        {{ ad.title }}
      </p>
    </div>

    <!-- 弹窗展示广告详情 -->
    <el-dialog :visible.sync="dialogVisible">
      <div>
        <h3>{{ currentAd.title }}</h3>
        <el-image
          :src="currentAd.image"
          alt="news image"
          style="width: 100%; height: auto; margin-bottom: 10px"
        />
        <p>{{ currentAd.details }}</p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      dialogVisible: false, // 控制弹窗的显示与隐藏
      currentAd: {}, // 当前选中的广告
      focusedIndex: null, // 当前聚焦的广告索引
      ads: [
        {
          title: "hello，点击获得黎樊的祝福",
          image: "https://img1.baidu.com/it/u=3742854494,3289314798&fm=253&app=138&size=w931&n=0&f=JPEG&fmt=auto?sec=1735664400&t=e06a39e8fc6c4737fa9bc4bec2d86031",
          details: "祝你2025新年快乐",
        },
        {
          title: "一张我的照片",
          image: "https://img2.baidu.com/it/u=1182930073,2868208470&fm=253&fmt=auto&app=138&f=JPEG?w=607&h=409",
          details: "嗯嗯啊啊哦哦呵呵",
        },
        {
          title: "这是我小时候的照片",
          image: "https://qlogo4.store.qq.com/qzone/2634933880/2634933880/100?1669802916",
          details: "哇哇哇小哥哥小时候好白好可爱",
        },
        {
          title: "点击查看最后一个内容",
          image: "https://img1.baidu.com/it/u=2436599813,4249934959&fm=253&fmt=auto&app=138&f=JPEG?w=600&h=400",
          details: "小伙伴们晚安",
        },
      ],
    };
  },

  mounted() {
    this.fetchAds();
  },

  methods: {
    setFocus(index) {
      this.focusedIndex = index;
    },
    removeFocus() {
      this.focusedIndex = null;
    },

    async HandleCilckAd(ad) {
      // 设置当前广告的信息并显示弹窗
      this.currentAd = ad;
      this.dialogVisible = true;
      // 上传点击广告的行为
      await this.logAdClick(ad);      
    },

    async logAdClick(ad) {
    try {
      const userId = localStorage.getItem("user_id");

      if (!userId) {
        console.error("User ID not found in localStorage");
        return;
      }

      const payload = {
        userId: userId,
        adId: ad.adId, 
        timestamp: new Date().toISOString(),
        userAction: "click",
      };

      const response = await axios.post("http://101.43.72.79:8088/api/ads/click", payload);
      
      console.log("广告点击统计成功:", response.data);
    } catch (error) {
      console.error("广告点击统计失败:", error);
    }
  },

    // 请求广告数据
    async fetchAds() {
      try {
        const userId = localStorage.getItem("user_id");

        if (!userId) {
          console.error("User ID not found in localStorage");
          return;
        }

        console.log("侧边广告请求时使用的userId：" + userId);

        const response = await axios.get("http://101.43.72.79:8088/api/ads/recommend", 
        {
          params: {
            userId: userId,
            limit: 6,
          },
        }
      );
        this.ads = response.data;
      } catch (error) {
        console.error("获取广告数据失败:", error);
      }
    },
  },
};
</script>

<style scoped>
.sider-news {
  padding: 3px;
  background: #fff;
}

.ad-item {
  margin-bottom: 10px;
  border: 1px solid #e0e0e0;
  border-radius: 0;
  overflow: hidden;
  background-color: #fafafa;
  padding: 0;
}

.ad-image {
  width: 100%;
  height: auto;
  display: block;
}

.ad-title {
  padding: 10px;
  font-size: 14px;
  text-align: left;
  color: #333;
  cursor: pointer;
  transition: color 0.3s ease, transform 0.3s ease;
}

.ad-title.focused {
  color: #0077ee; /* 鼠标悬停时变蓝 */
}

.ad-title:hover {
  color: #0077ee; /* 鼠标悬停时变蓝 */
}
</style>
